body {
  min-height: 100vh;
  display: grid;
  grid-template-columns: 2% 18% 20% 20% 20% 18% 2%;
  grid-template-rows: 50px 1fr auto 50px;
  font-family: "Roboto", sans-serif;
  font-size: 18px;
  font-weight: 400;
  line-height: 26px;
  margin: 0;
  color: #444;
  letter-spacing: 0.75px;
  background: #fff;
}

main {
  grid-row: 2;
  grid-column-start: 2;
  grid-column-end: 7;
  width: 100%;
  margin-top: 10px;
  margin-left: auto;
  margin-right: auto;
}

